import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import styled from 'styled-components';

const StyledGreet = styled.div`
  font-size: 3.5rem;

  @media (max-width: 768px) {
    font-size: 2rem;
  }
`;
const StyledName = styled.h1`
  margin: 0;
  background: linear-gradient(to right, #f09819, #ff512f);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 6.5rem;

  @media (max-width: 575.98px) {
    font-size: 3.5rem;
  }

`;
const StyledSubtitle = styled.div`
  font-size: 2rem;


  @media (max-width: 575.98px) {
    font-size: 1.5rem;
  }
`;

function Bio({ className }) {
  return (
    <StaticQuery
      query={bioQuery}
      render={(data) => {
        const { author, role } = data.site.siteMetadata;

        return (
          <div className={className}>
            <StyledGreet>Hello, my name is</StyledGreet>
            <StyledName>{author}</StyledName>
            <StyledSubtitle>{`A ${role} based in Sydney`}</StyledSubtitle>
          </div>
        );
      }}
    />
  );
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        role
        social {
          linkedin
          github
        }
      }
    }
  }
`;

export default Bio;
