import React from 'react';

import styled from 'styled-components';
import Bio from '../sections/Bio';
import Intro from '../sections/Intro';
import Skills from '../sections/Skills';
import Contact from '../sections/Contact';
import Layout from '../components/Layout';
import AnimatedArrow from '../components/AnimatedArrow';
import IndexBg from '../images/index-bg.png';


const HomeBanner = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  width: 100%;
  min-height: 200px;
  position: relative;
  height: 100vh;
  z-index: 1;

  :after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: radial-gradient(ellipse closest-side, rgba(15, 14, 22, 0.75), #100e17), url(${IndexBg});
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.75;
  }
`;
const HomeBannerItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const HomeBannerBio = styled(Bio)`
  margin:15px;
  padding:15px;
`;
const ScrollButton = styled(AnimatedArrow)`
  cursor:pointer;
`;

const ChildSection = styled.section`
  padding: 100px 0
`;


const Index = (props) => (
  <Layout location={props.location} theme="dark">
    <HomeBanner>
      <HomeBannerItem>
        <HomeBannerBio />
      </HomeBannerItem>
      <HomeBannerItem>
        <ScrollButton />
      </HomeBannerItem>
    </HomeBanner>

    <ChildSection>
      <div className="container">
        <Intro />
      </div>
    </ChildSection>

    <ChildSection>
      <div className="container">
        <Skills />
      </div>
    </ChildSection>


    <ChildSection>
      <div className="container">
        <Contact />
      </div>
    </ChildSection>

  </Layout>
);
export default Index;
